import React from "react";
import Button from '@mui/material/Button';
import CallMadeIcon from '@mui/icons-material/CallMade';
import videobg from "../Assests/main-video-min.mp4";
function FirstSection() {
  return (
    <section className="hero_sec">
      <div className="hero_text">
        <h1>Custom <span>Enterprise </span>Web Application Development Company </h1>
        <div className="btnes">
        <Button  className="btn_align" variant="outlined">Work Together <CallMadeIcon sx={{ fontSize: 20 }} className="arrow_icon"/></Button>

        </div>
      </div>
      <main className="video_sec">
        <video width="700" height="650" loop autoPlay muted>
          <source src={videobg} type="video/mp4" />
        </video>
      </main>
    </section>
  );
}

export default FirstSection;
