import React from "react";
import Button from '@mui/material/Button';
import CallMadeIcon from '@mui/icons-material/CallMade';
import SectionImg from "../Assests/work.png";
import SectionIm from "../Assests/ff1280f8a92108b05d689230ae90cea5.webp";
import SectionI from "../Assests/pics1.webp";
function SectionTwo() {
  return (
    <>
      <section className="Work_sec">
        <div className="workSec_first" id="portfolio">
          <h1>
            Work<span>.</span>
          </h1>
        </div>

        <div className="workSec_second">
          <div className="img_aligns">
            <img src={SectionImg} alt="" />
          </div>
          <div className="img_aligns">
            <img src={SectionIm} alt="" />
          </div>
          <div className="img_aligns">
            <img src={SectionI} alt="" />
          </div>
        </div>
        <div className="button_work">
        <Button  className="btn_align btn_work " variant="outlined">See More the Works <CallMadeIcon sx={{ fontSize: 20 }} className="arrow_icon btn_workinner"/></Button>
        </div>
      </section>
    </>
  );
}

export default SectionTwo;
