import React from 'react'
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import ImageForm from '../Assests/Checklist.jpg'
function Form() {
  return (
  <>
  <section className='form_sec'>
<div className="inner_form" id='form'>
  <h1>
  Let’s talk<span>.</span>
          </h1>
  <div className="main_form">
  <aside className='Fdetails_aside'>
    <h2>Let’s talk about your product
</h2>
<p>You can contact us via <a href="mailto:atuseef261@gmail.com">email</a> or using the form</p>
<div className="faside_image">
    <img src={ImageForm} alt="" />
</div>
   </aside>
   <aside className='form_aside'> <Box
      component="form"
      sx={{
        '& > :not(style)': { m: 1, width: '25ch' },
      }}
      noValidate
      autoComplete="off"
    >
      <TextField required id="outlined-basic" label="First name" variant="outlined" />
      <TextField required id="outlined-basic" label="Last name" variant="outlined" />
      <TextField id="outlined-basic" label="Company" variant="outlined" />
  
        <TextField
          required
          id="outlined-required"
          label="Email"
          defaultValue=""
          type="email"
      
        />

        <p>What are we building?
</p>
  <TextField
          id="outlined-multiline-static"
         
          multiline
          rows={12}
          className="widths_form"
          placeholder="Share a few details on your gaols, timeline, budget and others."
        />
        <Button  className="form_btn" variant="outlined">Send Request 
        </Button>  </Box>
    </aside>
  </div>
    </div>
    </section>
  </>
  )
}

export default Form